* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #1f2632;
  font-family: Manrope, sans-serif;
}

.advice {
  width: 90vw;
  height: inherit;
  background-color: #323a49;
  border-radius: 6px;
  margin: 15rem auto;
}

h1 {
  text-align: center;
  color: #52ffa8;
  padding-top: 1rem;
  font-size: 16px;
  font-weight: 800;
}

p {
  text-align: center;
  color: #cee3e9;
  margin-top: 1rem;
  padding: 1rem;
  font-size: 28px;
  font-weight: 800;
}

.images {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.dice__button {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  background-color: #52ffa8;
  border: none;
  border-radius: 50%;
  margin: .5rem auto;
  display: block;
  transform: translateY(50%);
}

.dice__button:hover {
  transition: all .2s;
  box-shadow: 0 0 40px 6px #52ffa8;
}

@media (min-width: 600px) {
  .advice {
    width: 40vw;
  }

  .line__divider {
    max-width: 38vw;
  }
}

